import { Component, OnInit } from '@angular/core';
import { AppService } from './../../app.service';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr'
import { Cookie } from 'ng2-cookies/ng2-cookies';



@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  public email: any;
  public password: any;
  constructor(public appService: AppService, public router: Router, private toastr: ToastrService) { }

  ngOnInit() {
  }

  public signinFunction: any = () => {
    if (!this.email) {
      this.toastr.warning('enter email')
    } else if (!this.password) {
      this.toastr.warning('enter password')
    } else {
      let data = {
        email: this.email,
        password: this.password
      }
      this.appService.signinFunction(data)
        .subscribe((apiResponse) => {
          if (apiResponse.status === 200) {
            this.toastr.success(apiResponse.message)
            Cookie.delete('authtoken');
            Cookie.delete('userName');
            Cookie.delete('email');
            Cookie.set('userName', apiResponse.data.userDetails.firstName + ' ' + apiResponse.data.userDetails.lastName);
            Cookie.set('authtoken', apiResponse.data.authToken);
            Cookie.set('email', apiResponse.data.userDetails.email);
            this.appService.setUserInfoInLocalStorage(apiResponse.data.userDetails)
            this.router.navigate(['/home']);
          } else {
            this.toastr.error(apiResponse.message)
          }
        }, (err) => {
          this.toastr.error(err.error.message)
        });
    } // end condition
  } // end signinFunction

}
